@custom-media --wide-vp (width >= 1024px);
@custom-media --not-wide-vp (width <= 1023.9px);
@custom-media --middle-vp (768px <= width < 1023.9px);
@custom-media --small-vp (width <= 767.9px);
@custom-media --not-small-vp (width >= 768px);
@custom-media --x2 (min-resolution: 2dppx);
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
:root {
  --regular-font: "Montserrat", "Impact", sans-serif;
}

:root {
  --color-base-light: #f9fbfd;
  --color-base-dark: #011c40;
  --color-bg-image: #00316a;
  --color-bg-advantages: #d9e6f5;
  --color-bg-reservation: #bbdffb;
  --color-gradient-advantages: rgba(227, 236, 246, 0.9);
  --color-gradient-overlay: rgba(0, 43, 102, 0.5);
  --color-bg-location: #f1efe9;
  --color-error: #ff8282;
  --color-shadow-screen: rgba(0, 0, 0, 0.25);
  --color-shadow-form: rgba(1, 28, 64, 0.15);
  --color-scroll: rgba(0, 0, 0, 0);
}

img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

.page {
  height: 100%;
  font-family: var(--regular-font);
  color: var(--color-base-dark);
  scroll-behavior: smooth;
}

.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--color-base-light);
}
.page__body--noscroll {
  position: fixed;
  height: 100vh;
  overflow: hidden;
  scrollbar-width: none;
  background: linear-gradient(var(--color-gradient-overlay), var(--color-gradient-overlay));
}
.page__body--noscroll::-webkit-scrollbar {
  display: none;
}
.page__body--noscroll .screen {
  z-index: -1;
}
.page__body--noscroll .page-header {
  background: linear-gradient(var(--color-gradient-overlay), var(--color-gradient-overlay)), url("../img/index-top-s@1x.jpg") no-repeat;
  background-size: cover;
  background-position: center;
}
@media (--x2) {
  .page__body--noscroll .page-header {
    background: linear-gradient(var(--color-gradient-overlay), var(--color-gradient-overlay)), url("../img/index-top-s@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}

.page__visual-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (--wide-vp) {
  .page__wrapper-center {
    box-sizing: border-box;
    max-width: 1250px;
    padding: 0 45px;
    margin: 0 auto;
  }
}
@media (--middle-vp) {
  .page__wrapper-center {
    margin: 0 45px;
  }
}
@media (--small-vp) {
  .page__wrapper-center {
    margin: 0 15px;
  }
}

.page-main {
  flex-grow: 1;
}

.page-main h2 {
  font-weight: 600;
  font-size: 28px;
  line-height: 1.5;
  margin-top: 0;
}
@media (--middle-vp) {
  .page-main h2 {
    font-size: 26px;
  }
}
@media (--small-vp) {
  .page-main h2 {
    font-size: 24px;
  }
}

.page-main h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
  margin-top: 0;
}

.page-header {
  position: relative;
  background-image: url("../img/index-top-l@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  background-color: var(--color-bg-image);
}
@media (--x2) {
  .page-header {
    background-image: url("../img/index-top-l@2x.jpg");
  }
}
@media (--wide-vp) {
  .page-header {
    box-shadow: 0 4px 4px 0 var(--color-shadow-screen);
    padding-bottom: 265px;
  }
}
@media (--middle-vp) {
  .page-header {
    padding-bottom: 263px;
    background-image: url("../img/index-top-m@1x.jpg");
  }
}
@media (--middle-vp) and (--x2) {
  .page-header {
    background-image: url("../img/index-top-m@2x.jpg");
  }
}
@media (--small-vp) {
  .page-header {
    padding-bottom: 165px;
    background-image: url("../img/index-top-s@1x.jpg");
  }
}
@media (--small-vp) and (--x2) {
  .page-header {
    background-image: url("../img/index-top-s@2x.jpg");
  }
}

@media (--not-small-vp) {
  .page-header__logo-link {
    margin-right: 60px;
    width: fit-content;
    max-width: 20%;
  }
}

.page-header__menu {
  display: flex;
  justify-content: space-between;
  padding: 20px 0 20px;
}
@media (--small-vp) {
  .page-header__menu {
    position: relative;
    justify-content: flex-start;
    padding: 10px 0;
  }
  .page-header__menu--opened {
    min-height: 42px;
    margin-left: 20px;
  }
  .page-header__menu--opened .page-header__menu-list {
    display: block;
    position: absolute;
    z-index: 2;
    top: 75px;
    left: 0;
    right: 0;
    width: calc(100% - 20px);
    height: 100vh;
    overflow: auto;
    box-shadow: 0 -75px 0 0 var(--color-base-light), 35px -75px 0 0 var(--color-base-light);
  }
  .page-header__menu--opened .page-header__menu-toggle {
    margin-right: -7px;
  }
  .page-header__menu--opened .page-header__menu-toggle:focus {
    outline: 2px solid var(--color-base-dark);
  }
  .page-header__menu--opened .page-header__menu-toggle:focus:not(:focus-visible) {
    outline: none;
  }
  .page-header__menu--opened .page-header__menu-toggle:focus-visible {
    outline: 2px solid var(--color-base-dark);
  }
  .page-header__menu--opened .page-header__menu-toggle::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: 5px;
    display: block;
    mask-repeat: no-repeat;
    mask-position: center;
    background: currentColor;
    width: 24px;
    height: 24px;
    mask-image: url("../icons/sprite.svg#toggle-closed");
    margin-right: 7px;
  }
  .page-header__menu--opened .page-header__menu-toggle::after {
    display: none;
  }
  .page-header__menu--nojs .page-header__menu-list {
    display: block;
    padding-top: 75px;
  }
  .page-header__menu--opened .page-header__logo-link, .page-header__menu--nojs .page-header__logo-link {
    position: absolute;
    top: 10px;
    left: 35px;
    z-index: 3;
  }
  .page-header__menu--opened .page-header__logo-link:focus, .page-header__menu--nojs .page-header__logo-link:focus {
    outline: 2px solid var(--color-base-dark);
    outline-offset: 2px;
  }
  .page-header__menu--opened .page-header__logo-link:focus:not(:focus-visible), .page-header__menu--nojs .page-header__logo-link:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }
  .page-header__menu--opened .page-header__logo-link:focus-visible, .page-header__menu--nojs .page-header__logo-link:focus-visible {
    outline: 2px solid var(--color-base-dark);
    outline-offset: 2px;
  }
  .page-header__menu--opened .page-header__logo-link {
    left: 15px;
  }
  .page-header__menu--opened .page-header__logo-link svg, .page-header__menu--nojs .page-header__logo-link svg {
    fill: var(--color-base-dark);
  }
}

@media (--wide-vp) {
  .page-header__menu-nav {
    padding-top: 20px;
  }
}
@media (--middle-vp) {
  .page-header__menu-nav {
    padding-top: 8px;
  }
}
@media (--small-vp) {
  .page-header__menu-nav {
    margin-top: -10px;
    margin-left: 20px;
    width: 100%;
  }
}

@media (--middle-vp) {
  .page-header__menu-list {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (--small-vp) {
  .page-header__menu-list {
    font-size: 18px;
    line-height: 22px;
    width: 100%;
    background-color: var(--color-base-light);
    box-shadow: 15px 0 0 0 var(--color-base-light);
    display: none;
  }
}

.page-header__menu-toggle {
  position: absolute;
  top: 15px;
  right: 0;
  margin-right: -2px;
  width: 30px;
  height: 30px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  text-align: center;
  z-index: 3;
}
.page-header__menu-toggle:focus {
  outline: 2px solid var(--color-base-light);
  outline-offset: 2px;
}
.page-header__menu-toggle:focus:not(:focus-visible) {
  outline: none;
  outline-offset: revert;
}
.page-header__menu-toggle:focus-visible {
  outline: 2px solid var(--color-base-light);
  outline-offset: 2px;
}
.page-header__menu-toggle:hover {
  opacity: 80%;
}
.page-header__menu-toggle:active {
  opacity: 60%;
}
@media (--small-vp) {
  .page-header__menu-toggle {
    display: block;
  }
}
.page-header__menu-toggle::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 5px;
  margin: auto;
  display: block;
  mask-repeat: no-repeat;
  mask-position: center;
  background: currentColor;
  color: var(--color-base-light);
  width: 24px;
  height: 24px;
  mask-image: url("../icons/sprite.svg#toggle-open");
}

@media (--not-small-vp) {
  .page-header__menu-list li {
    padding-top: 5px;
    margin-bottom: 5px;
  }
}
@media (--small-vp) {
  .page-header__menu-list li {
    margin-left: 15px;
  }
  .page-header__menu-list li:not(:last-child) {
    margin-bottom: 8px;
  }
}

@media (--small-vp) {
  .page-header__menu-list a {
    color: var(--color-base-dark);
    padding: 10px;
  }
  .page-header__menu-list a:focus {
    outline: 2px solid var(--color-base-dark);
    outline-offset: -2px;
  }
  .page-header__menu-list a:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }
  .page-header__menu-list a:focus-visible {
    outline: 2px solid var(--color-base-dark);
    outline-offset: -2px;
  }
  .page-header__menu-list a:hover {
    opacity: 80%;
  }
  .page-header__menu-list a:active {
    opacity: 60%;
  }
}

.page__footer-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  padding-top: 120px;
  padding-bottom: 120px;
}
@media (--middle-vp) {
  .page__footer-grid {
    gap: 30px;
    padding-top: 100px;
    padding-bottom: 100px;
  }
}
@media (--small-vp) {
  .page__footer-grid {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    padding-top: 59px;
    padding-bottom: 61px;
  }
}

@media (--small-vp) {
  .page-footer__logo-link {
    margin-bottom: 13px;
  }
}

.page-footer__location {
  background-color: var(--color-bg-location);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  min-height: 306px;
  margin-bottom: auto;
}
@media (--middle-vp) {
  .page-footer__location {
    min-height: 335px;
  }
}
@media (--small-vp) {
  .page-footer__location {
    grid-row: -2/-1;
    min-height: 316px;
  }
}

.page-footer__iframe {
  border: none;
  grid-row: 1/-1;
  grid-column: 1/-1;
  width: 100%;
  height: 306px;
}
@media (--middle-vp) {
  .page-footer__iframe {
    height: 335px;
  }
}
@media (--small-vp) {
  .page-footer__iframe {
    height: 316px;
  }
}

.page-footer__noscript {
  grid-row: 1/-1;
  grid-column: 1/-1;
}

.page-footer__contacts {
  margin-left: 60px;
}
@media (--not-wide-vp) {
  .page-footer__contacts {
    margin-left: 0;
  }
}
@media (--small-vp) {
  .page-footer__contacts {
    grid-row: 1/2;
  }
}

.page-footer__noscript img {
  display: block;
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 306px;
}
@media (--middle-vp) {
  .page-footer__noscript img {
    height: 335px;
  }
}
@media (--small-vp) {
  .page-footer__noscript img {
    height: 316px;
  }
}

.page-footer__container {
  background-color: var(--color-base-dark);
  padding: 35px 0 33px;
}
@media (--middle-vp) {
  .page-footer__container {
    padding: 41px 0;
  }
}
@media (--small-vp) {
  .page-footer__container {
    padding: 30px 0 19px;
  }
}

.page-footer__wrapper {
  display: flex;
}
@media (--small-vp) {
  .page-footer__wrapper {
    flex-direction: column;
    align-items: flex-start;
  }
}

.page-footer__main-nav {
  margin-right: auto;
  margin-left: 40px;
}
@media (--not-wide-vp) {
  .page-footer__main-nav {
    font-size: 12px;
    line-height: 15px;
  }
}
@media (--wide-vp) {
  .page-footer__main-nav {
    padding-top: 20px;
    width: 68%;
  }
}
@media (--middle-vp) {
  .page-footer__main-nav {
    padding-top: 6px;
    margin-left: 25px;
    width: 67%;
  }
}
@media (--small-vp) {
  .page-footer__main-nav {
    margin: 0;
  }
}

.page-footer__social {
  margin-right: 0;
}
@media (--wide-vp) {
  .page-footer__social {
    width: 188px;
    padding-top: 20px;
  }
}
@media (--middle-vp) {
  .page-footer__social {
    padding-top: 6px;
    width: 156px;
  }
}
@media (--small-vp) {
  .page-footer__social {
    margin-left: -4px;
  }
}

.button {
  font-family: var(--regular-font);
  font-weight: 600;
  font-size: 14px;
  line-height: 1.5;
  text-decoration: none;
  color: var(--color-base-dark);
  background-color: var(--color-base-light);
  border: none;
  user-select: none;
}